<template>
    <div class="boxBgc">
           <img src="../../assets/images/index/bgc.png" />
        <div class="box">
            <el-header>
                <el-row :gutter="20">
                    <el-col :span="23" class="names">
                        <div class="userimg">
                            <img src="../../assets/images/user.png"/>
                        </div>
                        <span>用户：{{userinfo}}</span>
                    </el-col>
                    <el-col :span="1">
                        <router-link :to="{ path: '/' }">
                            <div class="user_close">
                                X
                            </div>
                        </router-link>
                    </el-col>
                </el-row>
            </el-header>
            <el-main>
                <el-tabs :tab-position="tabPosition">
                    <el-tab-pane label="账号设置">
                        <div class="setuser">
                            <img src="../../assets/images/user.png"/>
                        </div>
                        <div class="setname">
                            <span>用户{{userinfo}}</span>
                        </div>
                        <div class="days">
                            剩余<span>{{times}}</span>天，请尽快充值，以免影响使用！
                        </div>
                        <div class="logoutuser">
                            <el-button @click="logoout">退出账号</el-button>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="充值记录">
                        <div>
                            <el-table
                                :data="tableData"
                                style="width: 100%">
                                <el-table-column
                                    prop="num"
                                    label="序号"
                                    width="80">
                                </el-table-column>
                                <el-table-column
                                    prop="card"
                                    label="卡号"
                                    width="380">
                                </el-table-column>
                                <el-table-column
                                    prop="money"
                                    label="金额"
                                    width="120">
                                </el-table-column>
                                <el-table-column
                                    prop="date"
                                    label="时间"
                                    width="200">
                                </el-table-column>
                                <el-table-column
                                    prop="state"
                                    label="状态">
                                </el-table-column>
                            </el-table>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="充值中心">
                        <div class="Voucher">
                            <el-form :label-position="labelPosition" label-width="80px">
                                <el-form-item label="账号:">
                                    <el-input  :value="userinfo" class="user_input"></el-input>
                                </el-form-item>
                                <el-form-item label="卡号:">
                                    <el-input :value="usercard" class="card_input"></el-input>
                                </el-form-item>
                                <el-form-item label="金额:">
                                    <el-input  class="money_input"></el-input>元
                                </el-form-item>
                                <el-form-item label="充值方式:" class="voucher_style">
                                    <el-radio-group>
                                        <el-radio label="支付宝"></el-radio>
                                        <el-radio label="微信"></el-radio>
                                    </el-radio-group>
                                </el-form-item>
                                <el-form-item>
                                    <el-button class="voucher_now" @click="voucherNow">立即充值</el-button>
                                </el-form-item>
                            </el-form>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </el-main>
        </div>
    </div>
</template>
<script>

export default {
    data() {
        return {
            userinfo: sessionStorage.getItem("userInfo"),
            usercard: sessionStorage.getItem("usercard"),
            labelPosition: 'right',
            tabPosition: 'left',
            tableData: [
                {
                    num: '1',
                    card: '12345678912345678912345678912345',
                    money: '1000',
                    date: '2020-05-02',
                    state: '充值成功',
                },
                {
                    num: '1',
                    card: '123456789123456789',
                    money: '1000',
                    date: '2020-05-02',
                    state: '充值成功',
                },
                {
                    num: '1',
                    card: '123456789123456789',
                    money: '1000',
                    date: '2020-05-02',
                    state: '充值成功',
                },
                {
                    num: '1',
                    card: '123456789123456789',
                    money: '1000',
                    date: '2020-05-02',
                    state: '充值成功',
                },
            ],
        };
    },
    methods:{
        //退出
        logoout:function(){
            this.$confirm('您确定要退出吗', '退出', {
                confirmButtonText: '确定',
                cancelButtonText:'取消',
                type: 'warning'
            }).then(() => {
                this.axios.get('/api/user/outlogin',{
                    params: {
                        mobile:this.userinfo
                    }
                })
                    .then((data) => {
                        let e = JSON.parse(data.data)
                        if (e.status == 'ok') {
                            localStorage.clear();
                            this.$router.push('/login')
                        } else {
                            this.$alert(e.msg, '错误提示', {
                                confirmButtonText: '确定'
                            })
                        }
                    }).catch((error) => {
                        console.log(error)
                    })
            }).catch(() => {

            });
        }
    }
};
</script>
<style src="../../assets/css/user.css" scoped>

</style>



